import { Client, Account, Storage, ID } from 'appwrite';

document.addEventListener('DOMContentLoaded', () => {
    console.log('Page loaded');

    const client = new Client()
        .setEndpoint('https://cloud.appwrite.io/v1')
        .setProject('67ddc1cd0024602b01e1'); // Your Project ID

    const account = new Account(client);
    const storage = new Storage(client);
    const bucketId = '67ddd15600244565f769';

    const authStatus = document.getElementById('authStatus');
    const loginForm = document.getElementById('loginForm');
    const emailInput = document.getElementById('emailInput');
    const passwordInput = document.getElementById('passwordInput');
    const loginBtn = document.getElementById('loginBtn');
    const registerBtn = document.getElementById('registerBtn');
    const logoutBtn = document.getElementById('logoutBtn');
    const fileInput = document.getElementById('fileInput');
    const uploadProgress = document.getElementById('uploadProgress');
    const spinner = document.getElementById('spinner');
    const searchInput = document.getElementById('searchInput');
    const bulkActions = document.getElementById('bulkActions');
    const deleteSelectedBtn = document.getElementById('deleteSelectedBtn');
    const downloadSelectedBtn = document.getElementById('downloadSelectedBtn');
    const fileList = document.getElementById('fileList');

    let allFiles = []; // Store all files for filtering

    async function checkAuth() {
        try {
            const user = await account.get();
            console.log('User logged in:', user);
            authStatus.textContent = `Logged in as ${user.email}`;
            loginForm.classList.add('hidden');
            logoutBtn.classList.remove('hidden');
            listFiles();
        } catch (error) {
            console.log('Not logged in:', error.message);
            authStatus.textContent = 'Not logged in';
            login: DLLForm.classList.remove('hidden');
            logoutBtn.classList.add('hidden');
            fileList.innerHTML = '';
        }
    }

    registerBtn.onclick = async () => {
        console.log('Register clicked');
        const email = emailInput.value;
        const password = passwordInput.value;
        if (!email || !password) {
            alert('Please enter an email and password!');
            return;
        }
        try {
            await account.create(ID.unique(), email, password);
            alert('Account created! Please log in.');
            emailInput.value = '';
            passwordInput.value = '';
        } catch (error) {
            alert('Registration failed: ' + error.message);
            console.error('Register error:', error);
        }
    };

    loginBtn.onclick = async () => {
        console.log('Login clicked');
        const email = emailInput.value;
        const password = passwordInput.value;
        if (!email || !password) {
            alert('Please enter an email and password!');
            return;
        }
        try {
            await account.createEmailPasswordSession(email, password);
            alert('Login successful!');
            checkAuth();
        } catch (error) {
            alert('Login failed: ' + error.message);
            console.error('Login error:', error);
        }
    };

    logoutBtn.onclick = async () => {
        console.log('Logout clicked');
        try {
            await account.deleteSession('current');
            alert('Logged out!');
            checkAuth();
        } catch (error) {
            alert('Logout failed: ' + error.message);
            console.error('Logout error:', error);
        }
    };

    window.uploadFile = async () => {
        console.log('Upload clicked');
        const files = fileInput.files;
        if (!files.length) {
            alert('Please select a file!');
            return;
        }
        uploadProgress.classList.remove('hidden');
        spinner.classList.remove('hidden');
        uploadProgress.innerHTML = '';
        const progressBars = {};

        try {
            for (let file of files) {
                const progressContainer = document.createElement('div');
                progressContainer.className = 'progress-container';
                progressContainer.innerHTML = `<span>${file.name}</span>`;
                const progressBar = document.createElement('div');
                progressBar.className = 'progress-bar';
                const progress = document.createElement('div');
                progress.className = 'progress';
                progressBar.appendChild(progress);
                progressContainer.appendChild(progressBar);
                uploadProgress.appendChild(progressContainer);
                progressBars[file.name] = progress;

                console.log('Uploading:', file.name);
                await storage.createFile(
                    bucketId,
                    ID.unique(),
                    file,
                    undefined,
                    (progressEvent) => {
                        const percent = Math.round((progressEvent.loaded / progressEvent.total) * 100);
                        progressBars[file.name].style.width = `${percent}%`;
                    }
                );
            }
            alert('Files uploaded!');
            uploadProgress.classList.add('hidden');
            spinner.classList.add('hidden');
            listFiles();
        } catch (error) {
            alert('Upload failed: ' + error.message);
            console.error('Upload error:', error);
            uploadProgress.classList.add('hidden');
            spinner.classList.add('hidden');
        }
    };

    async function listFiles(filter = '') {
        try {
            const response = await storage.listFiles(bucketId);
            console.log('Files retrieved:', response.files);
            allFiles = response.files;
            const filteredFiles = allFiles.filter(file => 
                file.name.toLowerCase().includes(filter.toLowerCase())
            );
            fileList.innerHTML = '';
            filteredFiles.forEach(file => {
                const li = document.createElement('li');
                
                const checkbox = document.createElement('input');
                checkbox.type = 'checkbox';
                checkbox.className = 'file-checkbox';
                checkbox.dataset.fileId = file.$id;
                li.appendChild(checkbox);

                if (file.name.match(/\.(jpg|jpeg|png)$/i)) {
                    const thumbnail = document.createElement('img');
                    thumbnail.src = storage.getFilePreview(bucketId, file.$id).href;
                    thumbnail.className = 'thumbnail';
                    li.appendChild(thumbnail);
                }

                const fileLink = document.createElement('span');
                fileLink.textContent = `${file.name} (${(file.sizeOriginal / 1024).toFixed(1)} KB)`;
                fileLink.className = 'file-link';
                fileLink.onclick = () => {
                    const fileView = storage.getFileView(bucketId, file.$id);
                    let fileUrl = fileView instanceof URL ? fileView.href : fileView;
                    console.log('Opening file URL:', fileUrl);
                    window.open(fileUrl, '_blank');
                };
                li.appendChild(fileLink);

                const downloadBtn = document.createElement('button');
                downloadBtn.textContent = 'Download';
                downloadBtn.className = 'download-btn';
                downloadBtn.onclick = () => {
                    const fileDownload = storage.getFileDownload(bucketId, file.$id);
                    let downloadUrl = fileDownload instanceof URL ? fileDownload.href : fileDownload;
                    console.log('Downloading file URL:', downloadUrl);
                    window.open(downloadUrl, '_blank');
                };
                li.appendChild(downloadBtn);

                const renameBtn = document.createElement('button');
                renameBtn.textContent = 'Rename';
                renameBtn.className = 'rename-btn';
                renameBtn.onclick = async () => {
                    const newName = prompt('Enter new file name:', file.name);
                    if (newName && newName !== file.name) {
                        try {
                            await storage.updateFile(bucketId, file.$id, newName);
                            alert('File renamed!');
                            listFiles(searchInput.value);
                        } catch (error) {
                            alert('Rename failed: ' + error.message);
                            console.error('Rename error:', error);
                        }
                    }
                };
                li.appendChild(renameBtn);

                const deleteBtn = document.createElement('button');
                deleteBtn.textContent = 'Delete';
                deleteBtn.className = 'delete-btn';
                deleteBtn.onclick = () => deleteFile(file.$id);
                li.appendChild(deleteBtn);

                fileList.appendChild(li);
            });
            updateBulkActionsVisibility();
        } catch (error) {
            console.error('List files failed:', error);
            alert('Failed to list files: ' + error.message);
        }
    }

    async function deleteFile(fileId) {
        console.log('Deleting file ID:', fileId);
        spinner.classList.remove('hidden');
        try {
            await storage.deleteFile(bucketId, fileId);
            console.log('File deleted successfully');
            alert('File deleted!');
            spinner.classList.add('hidden');
            listFiles(searchInput.value);
        } catch (error) {
            console.error('Delete error details:', error);
            alert('Delete failed: ' + error.message);
            spinner.classList.add('hidden');
        }
    }

    searchInput.oninput = () => {
        listFiles(searchInput.value);
    };

    function updateBulkActionsVisibility() {
        const checkboxes = document.querySelectorAll('.file-checkbox:checked');
        bulkActions.classList.toggle('hidden', checkboxes.length === 0);
    }

    fileList.addEventListener('change', (e) => {
        if (e.target.className === 'file-checkbox') {
            updateBulkActionsVisibility();
        }
    });

    deleteSelectedBtn.onclick = async () => {
        const selected = Array.from(document.querySelectorAll('.file-checkbox:checked'))
            .map(cb => cb.dataset.fileId);
        if (!selected.length) {
            alert('No files selected!');
            return;
        }
        if (confirm(`Delete ${selected.length} file(s)?`)) {
            spinner.classList.remove('hidden');
            try {
                await Promise.all(selected.map(fileId => storage.deleteFile(bucketId, fileId)));
                alert('Selected files deleted!');
                spinner.classList.add('hidden');
                listFiles(searchInput.value);
            } catch (error) {
                alert('Bulk delete failed: ' + error.message);
                console.error('Bulk delete error:', error);
                spinner.classList.add('hidden');
            }
        }
    };

    downloadSelectedBtn.onclick = () => {
        const selected = Array.from(document.querySelectorAll('.file-checkbox:checked'))
            .map(cb => cb.dataset.fileId);
        if (!selected.length) {
            alert('No files selected!');
            return;
        }
        selected.forEach(fileId => {
            const file = allFiles.find(f => f.$id === fileId);
            const fileDownload = storage.getFileDownload(bucketId, fileId);
            let downloadUrl = fileDownload instanceof URL ? fileDownload.href : fileDownload;
            console.log('Downloading selected file URL:', downloadUrl);
            window.open(downloadUrl, '_blank');
        });
    };

    checkAuth();
});